ul {
  list-style: none;
  padding: 0;
}

.movies-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  width: 1066px;
  gap: 50px;
}

.movies-item {
  border: solid 4px black;
  width: 500px;
  text-align: center;
}

.movies-item:last-child {
  margin-left: auto;
  margin-right: auto;
}

.movies-title {
  font-size: 25px;
}

.movies-genre,
.movies-director,
.movies-year {
  font-family: sans-serif;
  font-weight: 600;
}

.form {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  width: 500px;
}

.form-title {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  font-family: sans-serif;
}

.form-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-item {
  display: flex;
  justify-content: space-between;
  margin-right: auto;
  margin-left: auto;

  width: 250px;
}

label {
  font-family: sans-serif;
}

.add-movie {
  display: flex;
  margin-right: auto;
  margin-left: auto;
  padding: 5px 10px;
  background-color: rgb(0, 110, 255);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 5px;

  transition: all ease-in-out 200ms;
}

.add-movie:hover {
  opacity: 0.9;
}

.is-hidden {
  display: none;
}

.movies-managment {
  display: flex;
  margin-top: 20px;
  justify-content: center;
  gap: 20px;
}

.button {
  display: flex;
  padding: 5px 10px;
  background-color: rgb(0, 110, 255);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 5px;

  transition: all ease-in-out 200ms;
}

.button:hover {
  opacity: 0.9;
}
